@charset "utf-8";

/* (CJK)       . */
:lang(ko) {
  word-break: keep-all;
}

/*      */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; /*      */
}

body {
  overflow-wrap: break-word;
  line-height: 1.5;
  color: #333;
}

h1,
h2,
h3,
h4,
h5,
h6,
input,
textarea,
button,
select {
  font-size: inherit;
  line-height: inherit;
}

/*    */
ol,
ul {
  list-style: none;
}

/*    */
a {
  text-decoration: none;
  color: inherit;
}

/*    */
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/*     */
input,
textarea,
select,
button {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}

/*    */
table {
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
}

/* HTML5   */
article,
aside,
footer,
header,
nav,
section {
  display: block;
}

blockquote,
q {
  quotes: none;
}
blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

fieldset {
  border: none;
}

legend {
  font-size: 1em;
}

/*    */
button,
input,
select,
textarea {
  border: none;
  font: inherit;
  color: inherit;
  background: none;
  box-shadow: none;
  appearance: none;
  vertical-align: middle;
  font-size: 100%;
}

/*   */
button {
  cursor: pointer;
}

/* hr  */
hr {
  border: none;
  border-top: 1px solid #ccc;
  margin: 1em 0;
}
