.mypage-wrap {
  .order-list-header {
    display: flex;
    // justify-content: space-between; //   
    background-color: #f9f9f9; //  
  }

  .header-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 40px; //   
    border-bottom: 2px solid transparent; //   
    text-align: center;
    color: #333; //  
    text-decoration: none; //   
    font-size: 16px; //  

    &:hover {
      border-bottom: 2px solid $mainColor; //     
      color: $mainColor; //     
    }
  }
}
